<template>
    <div class="dtt-width-50 dtt-center">
        <el-row>
            <el-col>
                <el-form :inline="true">
                    <el-form-item label="项目名称">
                        <el-input v-model="searchText" placeholder="请输入项目名称"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button @click="getProjectList" icon="el-icon-search" type="primary"></el-button>
                    </el-form-item>
                </el-form>
            </el-col>
            <el-col class="dtt-border dtt-height-60 dtt-container-flex dtt-padding-left-10">
                <el-button type="primary" plain @click="dialogAddNewProjectVisible=true">创建项目</el-button>
            </el-col>
            <el-col>
                <el-table :data="datas" stripe :border="true">
                    <el-table-column type="index" label="编号" width="66"></el-table-column>
                    <el-table-column prop="name" label="项目名称"></el-table-column>
                    <el-table-column fixed="right" label="操作" width="120">
                        <template slot-scope="scope">
                            <!--<el-button type="text" size="small"-->
                                       <!--@click.native.prevent="askDeleteArticle(scope.$index)">删除</el-button>-->
                            <el-button type="text" size="small"
                                       @click.native.prevent="updateRow(scope.$index)">编辑</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </el-col>
        </el-row>

        <!--添加新项目 dialog-->
        <el-dialog title="添加新项目" :visible.sync="dialogAddNewProjectVisible">
            <add-new-project v-if="dialogAddNewProjectVisible" :mode="'add'" v-on:hide-dialog="dialogAddNewProjectVisible = $event"/>
        </el-dialog>

        <!--编辑项目-->
        <el-dialog title="编辑项目" :visible.sync="dialogEditProjectVisible">
            <add-new-project v-if="dialogEditProjectVisible" :mode="'edit'" :project="editProject" v-on:hide-dialog="editFinished($event)"/>
        </el-dialog>

        <!--删除项目-->
        <el-dialog title="提示" :visible.sync="dialogDeleteProjectVisible">
            <span>确认删除吗?</span>
            <span slot="footer" class="dialog-footer">
                        <el-button @click="dialogDeleteProjectVisible = false">取 消</el-button>
                        <el-button type="primary" @click="deleteRow(deleteIndex)">确 定</el-button>
                    </span>
        </el-dialog>
    </div>
</template>

<script>
    import AddNewProject from '../components/add.new.project.vue'
    import {TABLE_PROJECT} from '../utils/bmob.tables'

    export default {
        name: "project-list",
        components: {
            AddNewProject
        },
        data() {
            return {
                dialogAddNewProjectVisible: false,
                datas:[],
                dialogEditProjectVisible:false,
                dialogDeleteProjectVisible:false,
                editProject:{},
                searchText:""
            }
        },
        created() {
            this.getProjectList();
        },
        methods:{
            editFinished($event){
                this.dialogEditProjectVisible = $event;
                this.getProjectList();
            },
            // askDeleteArticle(index){
            //     this.dialogDeleteProjectVisible = true;
            // },
            updateRow(index){
                this.dialogEditProjectVisible = true;
                this.editProject = this.datas[index];
            },
            getProjectList(){
                const query = this.Bmob.Query(TABLE_PROJECT);
                let username = this.Bmob.User.current().username;
                let querys = [];
                const query1 = query.equalTo("user_name","==",username);
                querys.push(query1);
                if(this.searchText.length>0){
                    const query2 = query.equalTo("name","==",this.searchText);
                    querys.push(query2);
                }
                query.and(...querys);
                query.find().then(res => {
                    console.log(res);
                    this.datas = res;
                });
            }
        },
        mounted(){

        }
    }
</script>
