<template>
    <div id="add_new_project">
        <el-form ref="addProjectForm" :model="form" :rules="formRules" label-width="100px">
            <el-form-item label="项目名称" prop="projectName">
                <el-input v-model="form.projectName"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" class="dtt-width-30" @click="submit">提交</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    import uuid from 'uuid'
    export default {
        name: "add-new-project",
        props:{
            mode:{type:String},
            project:{type:Object}
        },
        data() {
            return{
                form: {
                    projectName: ""
                },
                formRules:{
                    projectName:{required:true,message:"请输入工程名称",trigger: 'blur'}
                }
            }
        },
        created() {
            if(this.mode === "edit") {
                this.form.projectName = this.project.name;
            }
        },
        mounted(){
        },
        methods:{
            submit(){
                this.$refs["addProjectForm"].validate((valid) => {
                    if(valid){
                        const query = this.Bmob.Query('table_project');
                        let objectId = this.Bmob.User.current().objectId;
                        let username = this.Bmob.User.current().username;
                        let acl = {};
                        acl[objectId] = {write:true};
                        acl["*"] = {read:true};
                        query.set("uuid",uuid());
                        query.set("name",this.form.projectName);
                        query.set("user_name",username);
                        query.set("ACL",acl);
                        if(this.mode === "edit"){
                            query.set("id", this.project.objectId);
                        }
                        query.save().then(res => {
                            console.log(res);
                            this.$message.success("添加成功");
                            this.$emit('hide-dialog',false);
                        }).catch(err => {
                            console.log(err);
                            this.$message.error(err.error);
                        })
                    }
                    else{
                        this.$message("表单校验失败，请检查");
                    }
                });
            }
        },
        computed:{
        }
    }
</script>
